import { Injectable } from '@angular/core';
import { Constants } from '../constants';
import { User } from '../_model';
import { plainToClass } from "class-transformer";
import { HttpClient } from '@angular/common/http';
import { map, timeout } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class UserService {
    //message
    constructor(private http: HttpClient) { }

    setCurrentUser(user: User): void {
        localStorage.setItem(Constants.STORAGE_ITEM__CURRENT_USER, JSON.stringify(user));
    }

    getCurrentUser(): User {
        let strCurrentUser: string = localStorage.getItem(Constants.STORAGE_ITEM__CURRENT_USER);
        if (strCurrentUser) {
            try {
                return plainToClass(User, JSON.parse(strCurrentUser) as Object);
            } catch {
                return null;
            }
        }

        return null;
    }
}