import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BarcodeFormat } from '@zxing/library';
import { ZXingScannerComponent } from '@zxing/ngx-scanner';

const PREVIOUS_SELECTED_CAMERA_LABEL: string = 'PREVIOUS_SELECTED_CAMERA_LABEL';

@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.sass']
})
export class ScannerComponent implements OnInit {
  @ViewChild(ZXingScannerComponent)
  scanner: ZXingScannerComponent;

  delayShowCheckingMessage: boolean = true;
  showScanner: boolean = false;
  isFirstTime: boolean = true;

  tryHarder: boolean = false;
  hasDevices: boolean;
  hasPermission: boolean;
  availableDevices: MediaDeviceInfo[];
  currentDevice: MediaDeviceInfo = null;

  formatsEnabled = [
    BarcodeFormat.CODE_39,
    BarcodeFormat.CODE_93,
    BarcodeFormat.CODE_128,
    BarcodeFormat.EAN_13,
    BarcodeFormat.EAN_8,
    BarcodeFormat.PDF_417,
    BarcodeFormat.DATA_MATRIX,
    BarcodeFormat.QR_CODE
  ];

  constructor(
    @Inject(MAT_DIALOG_DATA) readonly data: any,
    private readonly _dialogRef: MatDialogRef<ScannerComponent>
  ) {
  }

  ngOnInit(): void {
    setTimeout(()=>{
      this.delayShowCheckingMessage = false;
    }, 1000 * 2);
  }

  scanSuccess(value: string) {
    this.beep();
    this._dialogRef.close(value);
  }

  stopScan() {
    this._dialogRef.close();
  }

  onHasPermission(has: boolean) {
    this.hasPermission = has;
  }

  onCamerasFound(devices: MediaDeviceInfo[]): void {
    this.availableDevices = devices;
    this.hasDevices = Boolean(devices && devices.length);
  }

  onAutostarted() {
    this.isFirstTime = false;
    this.currentDevice = this.scanner.device;
    let previousSelectedCameraLabel: string = localStorage.getItem(PREVIOUS_SELECTED_CAMERA_LABEL);
    let currentSelectedCameraLabel: string = this.currentDevice.label;

    if (!previousSelectedCameraLabel) { // first time use scanner
      localStorage.setItem(PREVIOUS_SELECTED_CAMERA_LABEL, currentSelectedCameraLabel);
      this.showScanner = true;
    } else {  // not first time use scanner
      if (previousSelectedCameraLabel !== currentSelectedCameraLabel) { // previous selected camera is not same as the current default camera, change it to previous selected camera
        setTimeout(()=>{
          this.currentDevice = this.availableDevices.find(device => device.label === previousSelectedCameraLabel);
          this.showScanner = true;
        }, 1000 * 1);
      } else {
        this.showScanner = true;
      }
    }
  }

  onDeviceChange() {
    if (!this.isFirstTime) {
      localStorage.setItem(PREVIOUS_SELECTED_CAMERA_LABEL, this.currentDevice.label);
    }
  }

  beep(){
    let audio = new Audio();
    audio.src = "../../../assets/beep.wav";
    audio.load();
    audio.play();
  }
}
