import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from '../constants';
import { map, timeout } from 'rxjs/operators';
import { User } from '../_model';
import { UserService } from '../_services/user.service';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {
    constructor(
        private http: HttpClient,
        private router: Router,
        private userService: UserService) { }

    login(account: string, password: string) {
        /*
        return this.http.post<any>(Constants.API_ACCOUNT_LOGIN, { "Account": account, "Password": password })
          .pipe(timeout(Constants.TIMEOUT), map(res => {
            if (res.Status !== 'success') {
                //this.messageService.showNotice(res.Status,res.Message);
                return;
            }
            let user = new User(),
                pingTime = new Date().getTime();

            user.account = account;
            this.userService.setCurrentUser(user);

            localStorage.setItem(Constants.STORAGE_ITEM__LAST_TIME, pingTime.toString())
            return user;
        }));
        */
        let user = new User();
        user.account = account;
        this.userService.setCurrentUser(user);
    }

    logout(): void {
        this.clearStorageData();
        this.router.navigate(['/login']);
    }

    saveToken(token: string): void {
        localStorage.setItem(Constants.STORAGE_ITEM__TOKEN, token);
        localStorage.setItem(Constants.STORAGE_ITEM__TOKEN_CREATE_TIME, new Date().getMilliseconds().toString());
    }

    getToken(): string {
        return localStorage.getItem(Constants.STORAGE_ITEM__TOKEN);
    }

    getTokenCreateTime(): number {
        let tokenCreateTime: string = localStorage.getItem(Constants.STORAGE_ITEM__TOKEN_CREATE_TIME);
        if (tokenCreateTime === null || tokenCreateTime === undefined || tokenCreateTime === '') {
            return 0;
        }

        return Number(tokenCreateTime);
    }

    clearStorageData(): void {
        localStorage.removeItem(Constants.STORAGE_ITEM__CURRENT_USER);
        localStorage.removeItem(Constants.STORAGE_ITEM__IDLING);
        localStorage.removeItem(Constants.STORAGE_ITEM__IDLING_EXPIRY);
        localStorage.removeItem(Constants.STORAGE_ITEM__LAST_TIME);
        localStorage.removeItem(Constants.STORAGE_ITEM__TOKEN);
    }
}