import { AssetListComponent } from '../../secure/asset-list';
import { LocationListComponent } from '../../secure/location-list';
import { LocationComponent } from '../../secure/location';
import { ScannerComponent } from '../../secure/scanner';
import { Routes } from '@angular/router';

export const SECURE_ROUTES: Routes = [
    { path: '', redirectTo: 'location-list', pathMatch: 'full' },
    { path: 'asset-list', component: AssetListComponent },
    { path: 'location-list', component: LocationListComponent },
    { path: 'location', component: LocationComponent },
    { path: 'scanner', component: ScannerComponent }
];